<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">질문 등록</h2>
      </div>
    </div>
    <div class="section">
      <div class="container">
        제목: <input type="text" v-model="qna.btitle"><br>
        내용: <ckeditor v-model="qna.bcontent" :config="editorConfig"></ckeditor>
        <button class="btn btn-neutral"  style="color:blue" @click="add">작성완료</button>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';

export default {
  data() {
    return {
      qna:{
        btitle: '',
        bcontent: '',
        bwriter: ''
      },
      editorConfig:{
        language: 'ko'
      }
    }
  },
  methods: {
    add(){
      if(this.qna.btitle.trim() == '' || this.qna.bcontent.trim() == ''){
        alert('모든 항목을 다 입력해야됩니다.')
      }else{
        this.qna.bwriter = this.memberLoginInfo.info.userid;
        this.$store.dispatch("postQnA", this.qna).then(() => {
          this.$router.push("/qna");
        })
      }
    }
  },
  computed:{
    ...mapGetters(['memberLoginInfo'])
  }
}
</script>
<style lang="">
  
