<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">질문 게시판</h2>
      </div>
    </div>
    <div class="section" style="margin-top:-10px;height: 1000px">
      <div class="container">
        <table class="table">
          <tr>
            <th>번호</th>
            <th>제목</th>
            <th>작성자</th>
          </tr>
          <tr v-for="q in qnaList" :key="q.bnum">
            <th>{{q.bnum}}</th>
            <th><a href="#" @click="moveRead(q.bnum)">{{q.btitle}}</a></th>
            <th>{{q.bwriter}}</th>
          </tr>
        </table>
        <button class="btn btn-neutral"  style="color:blue" @click="moveWrite" v-if="memberLoginInfo.status">등록</button>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';

export default {
  name: 'qna',
  bodyClass: 'qna-page',
  components: {
  },
  created() {
    this.$store.dispatch("getQnAList");
  },
  computed:{
    ...mapGetters(['qnaList', 'memberLoginInfo'])
  },
  data() {
    return {
    }
  },
  methods: {
    moveRead(bnum){
      this.$store.dispatch("getQnA", bnum);
      console.log(bnum);
      this.$store.dispatch("getCommentList", bnum);
      this.$router.push("/qna/read");
    },
    moveWrite(){
      this.$router.push("/qna/write");
    }
  },
};
</script>
<style></style>
