<template lang="">
  <div class="container">
    <h1 style="font-family: 'Do Hyeon', sans-serif;">아파트 상세정보</h1>
    <table class="table table-bordered">
      <tr>
        <th>아파트명</th>
        <td>{{house.house.aptName}}</td>
      </tr>

      <tr>
        <th>위치</th>
        <td>{{house.house.city}} {{house.house.gugun}} {{house.house.dong}}</td>
      </tr>

      <tr>
        <th>매매</th>
        <td>{{house.house.dealAmount}},000원</td>
      </tr>

      <tr>
        <th>층수</th>
        <td>{{house.house.floor}}</td>
      </tr>

      <tr>
        <th>면적</th>
        <td>{{house.house.area|areafilter}}평</td>
      </tr>

      <tr>
        <th>건설년도</th>
        <td>{{house.house.buildYear}}</td>
      </tr>

      </table>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';

export default {
  data(){
    return{
      a:'',
    }
  },
  computed:{
    ...mapGetters(["house"]),
  }
  ,
  props:{
    no:String,
  },
  methods:{
    ...mapActions(["getHouse"]),
    houseInfo(no){
      this.getHouse(no);
    }
  },
  created() {
    this.houseInfo(this.no);
  },
  filters:{
    areafilter(value){
      value = value / 3.3;
      return Math.round(value);
    }
  }
}
</script>
