<template>
  <div>
    <div class="page-header clear-filter" filter-color="info">
      <parallax
        class="page-header-image page-header-small"
        style="background-image:url('img/my/main_house.jpg')"
      >
      </parallax>
      <div class="container">
        <div class="content-center brand" style="font-size:200%;font-family: 'Poor Story', cursive;">
          <h1 class="h1-seo" style="font-size: 100px">야 다방 어때 ?</h1>
          <h4>매매와 숙박을 한번에</h4>
        </div>
      </div>
    </div>
    <div class="main">
      <div class="container">
        <div class="row">
          <weather class="col-3"/>
          <div class="col-1"></div>
          <div class="section container col-8 mt-3">
            <h2 style="font-family: 'Do Hyeon', sans-serif;">공지사항</h2>
            <table class="table">
              <tr>
                <th>번호</th>
                <th>제목</th>
                <th>조회수</th>
              </tr>
              <tr v-for="n in noticeList" :key="n.nno" >
                <th>{{n.nno}}</th>
                <th><a href="#" @click="moveRead(n.nno)">{{n.ntitle}}</a></th>
                <th>{{n.nview}}</th>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>
<script>
import { Parallax } from '@/components';
import Weather from '@/components/Weather.vue';
import {mapGetters} from 'vuex';

export default {
  name: 'index',
  bodyClass: 'index-page',
  components: {
    Parallax,
    Weather,
  },
  created() {
    this.$store.dispatch("getNoticeList");
  },
  computed:{
    ...mapGetters(['noticeList'])
  },
  methods: {
    moveRead(nno){
      this.$store.dispatch("getNotice", nno);
      this.$router.push("/notice/read");
    },
  },
};
</script>
