<template lang="">
  <div class="section">
    <card style="width: 20rem;" class="container">
      <blockquote class="blockquote blockquote-primary mb-0 container" style="color:black">
        <div class="d-flex flex-column justify-content-center">
          <input type="text" v-model="keyword"><br>
          <button @click="moveWeather" class="btn btn-neutral"  style="color:blue;margin: -20px;">검색</button>
        </div>
        <div class="d-flex justify-content-center">
          <h4 class="card-title text-center">{{name}}</h4>
        </div>
        <div class="d-flex flex-row justify-content-around">
          <!-- <i class="wi wi-night-alt-snow" style="font-size: 30px;"></i> -->
          <p class="card-text">{{weather.description}}</p>
        </div>
        <div class="d-flex flex-row justify-content-around mt-3">
          <div>
            <i class="wi wi-strong-wind" style="font-size: 30px;"></i>
            <p class="card-text">{{wind}}m/s</p>
          </div>
          <div>
            <i class="wi wi-cloud" style="font-size: 30px;"></i>
            <p class="card-text">{{cloud}}%</p>
          </div>
        </div>
        <div class="d-flex flex-row justify-content-around mt-2">
          <div>
            <i class="wi wi-thermometer ml-2" style="font-size: 30px;"></i>
            <div class="d-flex flex-row">
              <p class="card-text">{{Math.round(main.temp - 274)}}</p>
              <i class="wi wi-celsius" style="font-size: 30px;"></i>
            </div>
            
          </div>
          <div>
            <i class="wi wi-humidity ml-2" style="font-size: 30px;"></i>
            <p class="card-text">{{main.humidity}}%</p>
          </div>
        </div>
      </blockquote>
    </card>
  </div>
</template>
<script>
import axios from 'axios';
import {Card} from '@/components/Cards/Card';

export default {
  component:{
    Card
  },
  created() {
    this.moveWeather();
  },
  data() {
    return {
      weather:{},
      main: {},
      wind: '',
      country: '',
      name: '',
      cloud: '',
      keyword:'Seoul',
    }
  },
  methods: {
    moveWeather(){
      axios.get("http://api.openweathermap.org/data/2.5/weather?q=" + this.keyword + "&appid=5a71ed675977b184cf813e7e7e949156").then((resp) => {
        console.log(resp);
        this.weather = resp.data.weather[0]; // main: 날씨, description: 날씨상세설명, icon:날씨 이미지
        this.main = resp.data.main; // temp - 273.15:온도, humidity: 습도
        this.wind = resp.data.wind.speed // 바람
        this.country = resp.data.sys.country // 나라이름
        this.name = resp.data.name // 도시
        this.cloud = resp.data.clouds.all // 구름
      })
    }
  },
}
</script>
<style lang="">
</style>