<script>
// import { Tabs, TabPane } from '@/components';
import {mapGetters} from 'vuex';

export default {
  name: 'profile',
  bodyClass: 'profile-page',
  components: {
    // Tabs,
    // TabPane
  },
  created() {
    console.log('Profile.vue created : '+this.memberLoginInfo.token);
    this.$store.dispatch("getProfile", this.memberLoginInfo.token).then((resp) => {
      console.log(resp);
      this.myInfo = resp.data.loginMember;
    }).catch(()=>{
      alert('로그인이 필요한 서비스입니다.');
      this.$router.push("/login");
    })
  },
  computed:{
    ...mapGetters(['memberLoginInfo'])
  },
  data() {
    return {
      isTrue: true,
      checkpwd: '',
      myInfo:{},
    }
  },
  methods: {
    checkPwd(){
      if(this.checkpwd === this.myInfo.userpwd){
        this.isTrue = false;
      }else{
        alert('비밀번호를 다시 입력해주세요.');
        this.checkpwd = '';
      }
    },
    moveDelete(){
      if(confirm("정말 삭제하시겠습니까?") == true){
        this.$store.dispatch("memberDelete", this.memberLoginInfo.token).then((resp) => {
          alert(resp.data);
          this.$store.dispatch("setmemberLoginInfo", {
            status: false,
            info:{},
            token: '',
          });
          this.$router.push("/");
        })
      }else{
        return;
      }
    },
    moveModify(){
      this.$router.push("/modify");
    }
  },
};
</script>
