<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">질문 게시판</h2>
      </div>
    </div>
    <div class="section">
      <div class="container">
        <h3 class="title">{{qna.btitle}}</h3>
        <p v-html="qna.bcontent"></p>
        <button class="btn btn-neutral"  style="color:blue" v-if="memberLoginInfo.info.role == 'admin'" @click="moveDelete(qna.bnum)">삭제</button>
        <button class="btn btn-neutral"  style="color:blue" @click="moveUpdate">수정</button>
        <table class="table">
          <tr>
            <th>내용</th>
            <th>작성자</th>
          </tr>
          <tr v-for="c in commentList" :key="c.cnum">
            <th v-html="c.ccontent"></th>
            <th v-text="c.cwriter"></th>
          </tr>
        </table>
        <form v-if="memberLoginInfo.status" >
          댓글: <input type="text" v-model="comment.ccontent">
          <button class="btn btn-neutral"  style="color:blue" @click.prevent="moveComment(qna.bnum)">댓글 작성</button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex';

export default {
  computed:{
    ...mapState(["qna", 'memberLoginInfo', 'commentList'])
  },
  methods: {
    moveUpdate(){ //수정은 이 글을 쓴 사람만
      if(this.qna.bwriter === this.memberLoginInfo.info.userid){
        this.$router.push("/qna/update")
      }else{
        alert('해당 글을 수정할 권한이 없습니다.');
      }
    },
    moveDelete(bnum){ // 관리자만 삭제할 수 있다.
      console.log(bnum);
      this.$store.dispatch("deleteQnA", bnum).then(() => {
        this.$store.dispatch("getQnAList");
        this.$router.push("/qna");
      })
    },
    moveComment(bnum){
      if(this.comment.ccontent.trim() == ''){
        alert('댓글 내용을 입력해주세요.')
      }else{
        this.comment.bnum = bnum;
        this.comment.cwriter = this.memberLoginInfo.info.userid;
        console.log(this.comment);
        
        this.$store.dispatch("postComment", this.comment).then(() => {
          this.$store.dispatch("getQnA", bnum);
          this.$store.dispatch("getCommentList", bnum);
        })
      }
      // this.comment.ccontent = '';
    }
  },
  created() {
    
  },
  data() {
    return {
      comment:{
        bnum: 0,
        cwriter: '',
        ccontent: '',
      }
    }
  },
}
</script>
<style lang="">
  
</style>