<template>
  <div class="page-header">
    <div
      class="page-header-image"
      style="background-image: url('img/my/night_login.jpg')"
    ></div>
    <div class="content">
      <div class="container">
        <h2 style="font-family: 'Do Hyeon', sans-serif;">회원가입</h2>
        <div class="col-md-5 ml-auto mr-auto">
          <card type="login" plain>
            <div class="container">

              <form>
                <div class="form-group d-flex flex-column">
                  <label for="userid">아이디:</label>
                  <input type="userid" class="form-control" placeholder="아이디를 입력하세요" id="userid" v-model="registerInfo.userid">
                </div>
                <div class="form-group d-flex flex-column">
                  <label for="userpwd">비밀번호:</label>
                  <input type="password" class="form-control" placeholder="비밀번호를 입력하세요" id="userpwd" v-model="registerInfo.userpwd">
                </div>
                <div class="form-group  d-flex flex-column">
                  <label for="username">닉네임:</label>
                  <input type="text" class="form-control" placeholder="닉네임을 입력하세요" id="username" v-model="registerInfo.username">
                </div>
                <div class="form-group  d-flex flex-column">
                  <label for="email">이메일:</label>
                  <input type="text" class="form-control" placeholder="이메일을 입력하세요" id="email" v-model="registerInfo.email">
                </div>
                <div class="form-group  d-flex flex-column">
                  <label for="address">주소:</label>
                  <input type="text" class="form-control" placeholder="주소를 입력하세요" id="address" v-model="registerInfo.address">
                </div>
                <div class="form-group d-flex flex-column">
                  <label for="age">나이:</label>
                  <select name="age" id="age" v-model="registerInfo.age">
                    <option value=10>10대</option>
                    <option value=20 selected>20대</option>
                    <option value=30>30대</option>
                    <option value=40>40대</option>
                    <option value=50>50대</option>
                    <option value=60>60대</option>
                  </select>
                </div>
                <div class="form-group d-flex flex-column">
                  <label for="role">회원:</label>
                  <select name="role" id="role" v-model="registerInfo.role">
                    <option value="user" selected>일반</option>
                    <option value="business" >사업자</option>
                  </select>
                </div>
                
                <button type="submit" class="btn btn-info" @click.prevent="moveSubmit">가입하기</button>
              </form>

            </div>
          </card>
        </div>
      </div>
    </div>
    <main-footer></main-footer>
  </div>
</template>
<script>
import { Card, Button, FormGroupInput } from '@/components';
import MainFooter from '@/layout/MainFooter';

export default {
  name: 'login-page',
  bodyClass: 'login-page',
  components: {
    Card,
    MainFooter,
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      registerInfo:{
        userid: '',
        userpwd: '',
        username: '',
        email: '',
        address: '',
        age: 20,
        role:'user',
      },
    }
  },
  methods: {
    moveSubmit(){
      if(this.registerInfo.userid.trim() == '' || this.registerInfo.userpwd.trim() == '' ||
        this.registerInfo.username.trim() == '' || this.registerInfo.email.trim() == '' ||
        this.registerInfo.address.trim() == ''){
          alert('모든 항목을 다 입력해야합니다.')
        }else{
          this.$store.dispatch("postRegister", this.registerInfo).then((resp) => {
            console.log(resp.data);
            if(resp.data === 'success_business'){
              this.$store.dispatch("addMsg", {status: "business", msg:"사업자 등록은 개별 이메일로 안내됩니다. 이메일을 확인해주세요."});
            }else if(resp.data === "success"){
              this.$store.dispatch("addMsg", {status: "success", msg:"회원가입 성공! 로그인 해보세요."});
            }else{
              this.$store.dispatch("addMsg", {status: "fail", msg:"회원가입 실패.. 다시 시도해보세요."});
            }

            this.$router.push("/login");
          })
        }
    }
  },
};
</script>
<style>
  label{
    text-align: left;
  }
</style>
