<template>
  <div class="page-header">
    <div
      class="page-header-image"
      style="background-image: url('img/my/night_login.jpg')"
    ></div>
    <div class="content">
      <div class="container">
        <div class="col-md-5 ml-auto mr-auto">

            
          <div v-if="msgInfo.status === 'business'">
            <alert type="warning" dismissible>
              <div class="alert-icon">
                  <i class="now-ui-icons ui-2_chat-round"></i>
              </div>
              <strong>{{msgInfo.msg}}</strong>
            </alert>
          </div>
          <div v-if="msgInfo.status === 'success'">
            <alert type="info" dismissible>
              <div class="alert-icon">
                  <i class="now-ui-icons ui-2_like"></i>
              </div>
              <strong>{{msgInfo.msg}}</strong>
            </alert>
          </div>
          <div v-if="msgInfo.status === 'fail'">
            <alert type="danger" dismissible>
              <div class="alert-icon">
                  <i class="now-ui-icons ui-2_like"></i>
              </div>
              <strong>{{msgInfo.msg}}</strong>
            </alert>
          </div>

          <card type="login" plain>
            <div class="container">
              <!-- <div slot="header" class="logo-container">
                <img v-lazy="'img/my/main_profile.png'" class="rounded-circle" alt="" />
              </div> -->
              <h2 style="font-family: 'Do Hyeon', sans-serif;">로그인</h2>
              <form class="mt-5">
                <div class="form-group">
                  <label for="userid">ID</label>
                  <input type="text" class="form-control" id="userid" placeholder="Enter ID" name="userid" v-model="memberInfo.userid">
                </div>
                <div class="form-group">
                  <label for="userpwd">Password</label>
                  <input type="password" class="form-control" id="userpwd" placeholder="Enter password" name="userpwd" v-model="memberInfo.userpwd">
                </div>
                
                <div class="card-footer text-center mt-5">
                  <a
                    href="#"
                    class="btn btn-info btn-round btn-lg btn-block" @click.prevent="moveLogin"
                    >로그인</a
                  >
                </div>
                <div class="pull-left">
                  <h5>
                    <a href="#" class="link footer-link" @click.prevent="moveRegister">회원 가입</a>
                  </h5>
                </div>
                <div class="pull-right">
                  <h5>
                    <a href="#" class="link footer-link">도움이 필요하신가요?</a>
                  </h5>
                </div>
              </form>
            </div>
          </card>
        </div>
      </div>
    </div>
    <main-footer></main-footer>
  </div>
</template>
<script>
import { Card, Button, FormGroupInput } from '@/components';
import MainFooter from '@/layout/MainFooter';
import {mapGetters} from 'vuex';
import { Alert } from '@/components';

export default {
  name: 'login-page',
  bodyClass: 'login-page',
  computed:{
    ...mapGetters(['msgInfo'])
  },
  components: {
    Card,
    MainFooter,
    Alert,
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      memberInfo:{
        userid: "",
        userpwd: "",
      }
    }
  },
  methods: {
    moveLogin(){
      if(this.memberInfo.userid.trim() == '' || this.memberInfo.userpwd == ''){
        alert('아이디와 비밀번호를 입력하세요.');
      }else{
        this.$store.dispatch("postMember", this.memberInfo).then((res) => {
        console.log(res);
        if(res.data.status){
          this.$store.dispatch("setmemberLoginInfo", {
            status: res.data.status,
            info: res.data.data,
            token: res.data.jwt_auto_token
          })
          this.$router.push("/")
        }
        }).catch(() => {
          alert('아이디와 비밀번호를 다시 확인해주세요!!');
          this.memberInfo.userid = '';
          this.memberInfo.userpwd='';
        })
      }
      this.$store.dispatch("addMsg", {status: "", msg:""});
    },
    moveRegister(){
      this.$router.push("/register");
      this.$store.dispatch("addMsg", {status: "", msg:""});
    }
  },
};
</script>
<style></style>
