<template>
  <div class="container">
    
    <div id="map" style="width:100%; height:500px;"></div>
  </div>
</template>

<script>
export default {
  props:[
    'lat',
    'lng',
  ],
  mounted() {
    if(window.kakao && window.kakao.maps){
        this.initMap()
      }else{ 
        this.addScript();
      }
  },
  methods: {
    initMap() {
      console.log('init map?!');
      var mapContainer = document.getElementById('map'); // 지도를 표시할 div
      var mapOption = {
          center: new kakao.maps.LatLng(parseFloat(this.lat),parseFloat(this.lng)), // 지도의 중심좌표
          level: 3, // 지도의 확대 레벨
      };

      var map = new kakao.maps.Map(mapContainer, mapOption);

      var marker = new kakao.maps.Marker({
          // map: map, // 마커를 표시할 지도
          position: new kakao.maps.LatLng(parseFloat(this.lat),parseFloat(this.lng)), // 마커를 표시할 위치
      });
      marker.setMap(map);
    },
    addScript() {
      const script = document.createElement('script'); 
      script.onload = () => kakao.maps.load(this.initMap); 
      script.src = 'http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=e9d25545815b9fa054fba77bf7aa2e29'; 
      document.head.appendChild(script); 
    },
  },
  watch:{
    lat(){
      if(window.kakao && window.kakao.maps){
        this.initMap()
      }else{ 
        this.addScript();
      }

    this.initMap();
    }
  }
  
}
</script>
