<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">공지사항 수정</h2>
      </div>
    </div>
    <div class="section">
      <div class="container">
        번호: <input type="text" v-model="notice.nno" disabled><br>
        제목: <input type="text" v-model="notice.ntitle"><br>
        내용: <ckeditor v-model="notice.ncontent" :config="editorConfig"></ckeditor>
        <button class="btn btn-neutral"  style="color:blue" @click="update">수정완료</button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'

export default {
  computed:{
    ...mapState(["notice"])
  },
  methods: {
    update(){
      if(this.notice.ntitle.trim() == '' || this.notice.ncontent.trim() == ''){
        alert('모든 항목을 다 입력해야됩니다.')
      }else{
        this.$store.dispatch("updateNotice").then(() => {
          this.$store.dispatch("getNoticeList");
          this.$router.push("/notice");
        })
      }
    }
  },
  data() {
    return {
      editorConfig:{
        language: 'ko'
      }
    }
  },
}
</script>
