<template lang="">
  <div class="container">
    <div id='example-3'>
  <input type="checkbox" id="cafe" value="cafe" v-model="checkedNames">
  <label for="cafe">카페</label>
  <input type="checkbox" id="john" value="mart" v-model="checkedNames">
  <label for="mart">편의점</label>
  <br>
</div>
    <div id="map" style="width:500px;height:400px;"></div>
  </div>
</template>
<script>
import {mapState} from 'vuex';
export default {
  data(){
    return{
      checkedNames:[],
    }
  },
  computed:{
    ...mapState(['house']),
  },
  // mounted() {
  // window.kakao && window.kakao.maps ? this.initMap() : this.addScript();

  // },
  methods: {
    initMap() {
    var mapContainer = document.getElementById('map'); // 지도를 표시할 div
    var mapOption = {
          center: new kakao.maps.LatLng(parseFloat(this.house.place.lat),parseFloat(this.house.place.lng)), // 지도의 중심좌표
          level: 3, // 지도의 확대 레벨
        };

    var map = new kakao.maps.Map(mapContainer, mapOption);

    var imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png"; 
    var cafeImage = "img/cafeimg.png";
    var cafe = false;
    var mart = false;
    for(var j=0;j<this.checkedNames.length;j++){
      if(this.checkedNames[j]==='cafe'){
        cafe = true;    
      }
      else if(this.checkedNames[j]==='mart'){
        mart = true;
      }
    }

    
      for(var i=0; i < this.house.cList.length; i++){
          var imageSize = new kakao.maps.Size(24, 35); 
       // 마커 이미지를 생성합니다
          var markerImage;
          if(cafe && mart){
            if(this.house.cList[i].ctype==='커피전문점/카페/다방' || this.house.cList[i].ctype==='Q12A01'){
              markerImage = new kakao.maps.MarkerImage(cafeImage, imageSize); 
            }
            else if(this.house.cList[i].ctype==='편의점' || this.house.cList[i].ctype==='D03A01'){
              markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);
            }
            else{
              continue;
            }
          }
          else if(cafe){
            if(this.house.cList[i].ctype==='커피전문점/카페/다방' || this.house.cList[i].ctype==='Q12A01'){
              markerImage = new kakao.maps.MarkerImage(cafeImage, imageSize); 
            }
            else{
              continue;
            }
          }
          else if(mart){
            if(this.house.cList[i].ctype==='편의점' || this.house.cList[i].ctype==='D03A01'){
              markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);
            }
            else{
              continue;
            }
          }
          else{
            if(this.house.cList[i].ctype==='커피전문점/카페/다방' || this.house.cList[i].ctype==='Q12A01'){
              markerImage = new kakao.maps.MarkerImage(cafeImage, imageSize); 
            }
            else if(this.house.cList[i].ctype==='편의점' || this.house.cList[i].ctype==='D03A01'){
              markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);
            }
            else{
              markerImage = '';
            }
          }
       // 마커를 생성합니다
          var marker = new kakao.maps.Marker({
          map: map, // 마커를 표시할 지도
          position: new kakao.maps.LatLng(this.house.cList[i].lat,this.house.cList[i].lng), // 마커를 표시할 위치
          title: this.house.cList[i].cname,
          image : markerImage // 마커 이미지 
          });
          var iwContent = `<div style="padding:5px;"><b>${this.house.cList[i].cname}</b> <br> ${this.house.cList[i].ctype}</div>`; // 인포윈도우에 표출될 내용으로 HTML 문자열이나 document element가 가능합니다
          var infowindow = new kakao.maps.InfoWindow({
            content : iwContent 
          });

          kakao.maps.event.addListener(marker, 'mouseover', makeOverListener(map, marker, infowindow));
          kakao.maps.event.addListener(marker, 'mouseout', makeOutListener(infowindow));
          marker.setMap(map);
      }
    

    function makeOverListener(map, marker, infowindow) {
      return function() {
          infowindow.open(map, marker);
    };
  }

// 인포윈도우를 닫는 클로저를 만드는 함수입니다 
    function makeOutListener(infowindow) {
      return function() {
          infowindow.close();
    };
}
    return map;
    },
    addScript() {
      const script = document.createElement('script');
      script.onload = () => kakao.maps.load(this.initMap); 
      script.src = 'http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=e9d25545815b9fa054fba77bf7aa2e29'; 
      document.head.appendChild(script); 
    },
    makeOverListener(map, marker, infowindow){
      infowindow.open(map, marker);
    },
    makeOutListener(infowindow){
      infowindow.close();
    }
  },
  updated() {
    this.initMap();
  },
  watch:{
    house(){
      window.kakao && window.kakao.maps ? this.initMap() : this.addScript();
    }
  }
}
</script>
<style lang="">
  
</style>