<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">매매 정보</h2>
      </div>
    </div>
    <div class="section section-about-us">
      <div class="container">
        <div class="row">
          <div class="col-md-8 ml-auto mr-auto text-center">
            <h2 class="title" style="font-family: 'Do Hyeon', sans-serif;">조건에 맞는 건물을 검색해보세요</h2>
            <div class="container align-center">
              
              <div class="row" style="text-align:center">
                <select v-model="opt" style="width:200px">
                  <option value="AptName">아파트이름</option>
                  <option value="dong">동</option>
                  <option value="buildYear">건설년도</option>
                  <option value="floor">층수</option>
                </select>
                <fg-input class="col-9 mt-3" placeholder="" v-model="val"></fg-input>
                <!-- <div class="col-1"></div> -->
                <n-button class="col-2" type="info" @click="searchPage(1)">Search</n-button>
              </div>
              <table class="table table-striped">
      <tr>
        <th>아파트명</th>
        <th>동</th>
        <th>건설년도</th>
        <th>면적</th>
        <th>층</th>
        <th>가격</th>
      </tr>
      <tr v-for="house in houses.bList" :key="house.no">
        <td><a href="" @click="moveRead(house.no)">{{house.aptName}}</a></td>
        <td>{{house.dong}}</td>
        <td>{{house.buildYear}}</td>
        <td>{{house.area}}</td>
        <td>{{house.floor}}</td>
        <td>{{house.dealAmount}}</td>
      </tr>
    </table>
    <template v-if="opt=='' || val==''">
      <div class="d-flex justify-content-between">
      <ul class="pagination">
        <li class="page-item"><a class="page-link" href="#" @click="sendPage(page-1)">Previous</a></li>
        <li class="page-item" v-for="(p, index) in plist()" :key="`${index}_plist`">
          <a class="page-link" href="#" @click="sendPage(p)">{{p}}</a>
        </li>
        <li class="page-item"><a class="page-link" href="#" @click="sendPage(page+1)">Next</a></li>
      </ul>
      </div>
    </template>
    <template v-else>
      <div class="d-flex justify-content-between">
      <ul class="pagination">
        <li class="page-item"><a class="page-link" href="#" @click="sendSearchPage(page-1)">Previous</a></li>
        <li class="page-item" v-for="(p, index) in plist()" :key="`${index}_plist`">
          <a class="page-link" href="#" @click="sendSearchPage(p)">{{p}}</a>
        </li>
        <li class="page-item"><a class="page-link" href="#" @click="sendSearchPage(page+1)">Next</a></li>
      </ul>
      </div>
    </template>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, FormGroupInput } from '@/components';
import {mapGetters} from "vuex";
import {mapActions} from "vuex";
export default {
  name: 'landing',
  bodyClass: 'landing-page',
  components: {
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      form: {
        firstName: '',
        email: '',
        message: ''
      },
      opt:'',
      val:'',
      page: '1',
      plist: function () {
          var list = [];
          for (var i = this.houses.startPage; i <= this.houses.endPage; i++)
            list.push(i);
          return list;
      }
    };
  },
  computed: {
    ...mapGetters(["houses"]),
  },
  methods:{
    ...mapActions(["getHouses"]),
    createList(page){
      this.getHouses(page);
    },
    sendPage(page){
        if(page<=1) page = 1;
        if(page >= this.houses.totalPage) page = this.houses.totalPage;
        this.page = page;

        this.createList(page);
        if (page % 10 == 0) this.houses.startPage = page + 1;
    },
    ...mapActions(["getSearchHouses"]),
    searchList(page){
      let pageInfo = {
        page: page,
        opt: this.opt,
        val: this.val,
      }
      this.getSearchHouses(pageInfo);
    },
    searchPage(page){
      if(this.opt=='' || this.val==''){
        return;
      }
      this.searchList(page);
    },
    sendSearchPage(page){
        if(page<=1) page = 1;
        if(page >= this.houses.totalPage) page = this.houses.totalPage;
        this.page = page;
        let pageInfo = {
        page: page,
        opt: this.opt,
        val: this.val,
      }
      this.getSearchHouses(pageInfo);
      if (page % 10 == 0) this.houses.startPage = page + 1;
    },
    ...mapActions(["getHouse"]),
    HouseInfo(no){
      this.getHouse(no);
    },
    moveRead(no){
      this.HouseInfo(no);
      this.$router.push("/houseread?no="+no);
    },
  },
  created() {
    this.createList(1);
  },
};
</script>
<style></style>
