<template>
  <div>
    <div class="page-header" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">공지사항</h2>
      </div>
    </div>
    <div class="section" style="margin-top:-10px;height: 1000px">
      <div class="container">
        <table class="table">
          <tr>
            <th>번호</th>
            <th>제목</th>
            <th>조회수</th>
          </tr>
          <tr v-for="n in noticeList" :key="n.nno">
            <th>{{n.nno}}</th>
            <th><a href="#" @click="moveRead(n.nno)">{{n.ntitle}}</a></th>
            <th>{{n.nview}}</th>
          </tr>
        </table>
        <button class="btn btn-neutral"  style="color:blue" v-if="memberLoginInfo.info.role == 'admin'" @click="moveWrite">등록</button>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';

export default {
  name: 'profile',
  bodyClass: 'profile-page',
  components: {
  },
  created() {
    this.$store.dispatch("getNoticeList");
  },
  computed:{
    ...mapGetters(['noticeList', 'memberLoginInfo'])
  },
  data() {
    return {
  
    }
  },
  methods: {
    moveRead(nno){
      this.$store.dispatch("getNotice", nno);
      this.$router.push("/notice/read");
    },
    moveWrite(){
      this.$router.push("/notice/write");
    }
  },
};
</script>
<style></style>
