<template> 
  <div>
    <div>총 {{totalCount}}건<hr></div>
    <div>
      <ul class="list-group list-group-flush">
        <li v-for="(item,index) in tripInfo" :key="index" class="mb-3">
          <a href="#" @click="detailInfo(item)" class="d-flex flex-row" style="color:black">
            <img v-if="item.firstimage" :src="item.firstimage"/>
            <img v-else src="/img/my/cat_no_image.jpg"/>
            <div>
              <h3 class="ml-5 mt-2" style="font-family: 'Do Hyeon', sans-serif;">{{item.title}}</h3>
              <p class="ml-5 mt-0">조회수 {{item.readcount}}</p>
            </div>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
import axios from 'axios';

export default {
  components: {
    
  },
  computed:{
    ...mapGetters(['cityCode', 'tripInfo', 'tripDetailInfoCommon', 'tripDetailInfoIntro', 'tripDetailInfoCourse', 'tripMapInfo', 'tripPageInfo']),
    changCode(){ return this.cityCode},
    changePage(){return this.tripPageInfo.now}
  },
  watch:{
    // tripPageInfo(){
    //   this.setList();
    // },
    changCode(){
      this.setList();
    },
    chagePage(){
      console.log(this.tripPageInfo.now);
      this.setList();
    }
  },
  created() {
    this.setList();
  },
  methods: {
    setList(){
      console.log("code: " + this.cityCode);
      console.log("page: " + this.tripPageInfo.now);
      axios.get("http://api.visitkorea.or.kr/openapi/service/rest/KorService/areaBasedList?ServiceKey=" +
          "FNrbdF63%2FLGeHjLM2O5dQwnRa1aJF79Ten4AI10VtPFnbN2U8SSwr6GMc4CXxWxVPnsEuzEkSTvGf2XHHJhSRw%3D%3D&contentTypeId=25" +
          "&areaCode=" + this.cityCode + "&sigunguCode=&cat1=&cat2=&cat3=&listYN=Y&MobileOS=ETC&MobileApp=TourAPI3.0_Guide&arrange=A&numOfRows=12&pageNo=" + this.tripPageInfo.now).then((resp) => {
          console.log(resp);
          this.$store.dispatch("setTripInfo", resp.data.response.body.items.item);
          this.totalCount = resp.data.response.body.totalCount;
          this.count = resp.data.response.body.numOfRows;
          this.$store.dispatch("setTripPageInfo", {now: resp.data.response.body.pageNo, total: Math.round(resp.data.response.body.totalCount/resp.data.response.body.numOfRows)+1})
      })
      console.log("get page: " + this.tripPageInfo.now);
    },
    detailInfo(item){
      this.$store.dispatch("setTripMapInfo", {mapx: item.mapx, mapy: item.mapy, mlevel:item.mlevel});

      axios.get("http://api.visitkorea.or.kr/openapi/service/rest/KorService/detailCommon?ServiceKey="
        + "FNrbdF63%2FLGeHjLM2O5dQwnRa1aJF79Ten4AI10VtPFnbN2U8SSwr6GMc4CXxWxVPnsEuzEkSTvGf2XHHJhSRw%3D%3D&contentTypeId=25&contentId=" + item.contentid
        + "&MobileOS=ETC&MobileApp=TourAPI3.0_Guide&defaultYN=Y&firstImageYN=Y&areacodeYN=Y&catcodeYN=Y&addrinfoYN=Y&mapinfoYN=Y&overviewYN=Y&transGuideYN=Y").then((resp) => {
          console.log('공통정보');
          console.log(resp);
          this.$store.dispatch("setTripDetailInfoCommon", resp.data.response.body.items.item);
      })

      axios.get("http://api.visitkorea.or.kr/openapi/service/rest/KorService/detailIntro?ServiceKey="
        + "FNrbdF63%2FLGeHjLM2O5dQwnRa1aJF79Ten4AI10VtPFnbN2U8SSwr6GMc4CXxWxVPnsEuzEkSTvGf2XHHJhSRw%3D%3D"
        + "&contentTypeId=25&contentId="+ item.contentid + "&MobileOS=ETC&MobileApp=TourAPI3.0_Guide&introYN=Y").then((resp) => {
          console.log('소개 정보');
          console.log(resp);
          this.$store.dispatch("setTripDetailInfoIntro", resp.data.response.body.items.item);
        })

      axios.get("http://api.visitkorea.or.kr/openapi/service/rest/KorService/detailInfo?ServiceKey="
        + "FNrbdF63%2FLGeHjLM2O5dQwnRa1aJF79Ten4AI10VtPFnbN2U8SSwr6GMc4CXxWxVPnsEuzEkSTvGf2XHHJhSRw%3D%3D"
        + "&contentTypeId=25&contentId=" + item.contentid + "&MobileOS=ETC&MobileApp=TourAPI3.0_Guide&listYN=Y").then((resp) => {
          console.log('코스정보');
          console.log(resp);
          this.$store.dispatch("setTripDetailInfoCourse", resp.data.response.body.items.item);
        })
        
      this.$router.push("/trip/detail");
    }
  },
  data() {
    return {
      totalCount: 0,
      count: 0,
    }
  },
};
</script>

<style>
.tab-content.tab-content-padding {
  padding: 20px;
}

img {
  width: 100px;
  height: 100px;
}

ul {
  list-style:none;
}
</style>
