<template lang="">
  <div>
    <div class="container">
        <div class="">
          <table v-if="roominfo">
            <tr>
              <td colspan="2" rowspan="2"><img class="first" :src="cominfo.firstimage" alt=""></td>
              <td><img class="other" :src="roominfo[0].roomimg1" alt=""></td>
              <td><img class="other" :src="roominfo[0].roomimg2" alt=""></td>
            </tr>
            <tr>
              <td><img class="other2" :src="roominfo[0].roomimg3" alt=""></td>
              <td><img class="other2" :src="roominfo[0].roomimg4" alt=""></td>
            </tr>
          </table>
        </div>
        <div>
        <div id="wrapper">
        <div class="pt-3 box1">
          <div class="box_main box_line">
            <h2 style="font-family: 'Do Hyeon', sans-serif;">{{cominfo.title}}</h2>
            <div class="pb-3">{{cominfo.overview}}</div>
          </div>
          <div class="pt-3 box_main box_line">
            <div class="">
              <i class="fa fa-phone pr-2" style="font-size:24px;"></i>&nbsp;
              <span class="intro" >문의 : {{intinfo.infocenterlodging}}</span><br><br>
              <i class="fa fa-chevron-right pr-2" style="font-size:24px;"></i>&nbsp;
              <span class="intro" >체크인 : {{intinfo.checkintime}}</span><br><br>
              <i class="fa fa-chevron-left pr-2" style="font-size:24px;"></i>&nbsp;
              <span class="intro" >체크아웃 : {{intinfo.checkouttime}}</span><br><br>
              <i class="fa fa-krw" style="font-size:24px;"></i>&nbsp;
              <span class="intro" v-if="roominfo" >가격 : {{roominfo[0].roomoffseasonminfee1}}(원) (2인)<br><br></span>
              <span class="intro" v-if="typeof(roominfo)=='undefined'">가격 : 62000(원) (2인)<br><br></span>
              <div class="pb-2"></div>
            </div>
          </div>  
          <div class="">
            <div class="box_main">
              <div class="box box_line">
                <h2 class="pt-3"  style="font-family: 'Do Hyeon', sans-serif;">편의시설</h2><br>
                <div class="box3">
                  <i class="fa fa-car pr-2" style="font-size:24px;"></i>
                  <span style="font-size:20px;">&nbsp;주차</span><br><br>
                  <i class="fa fa-television pr-2" style="font-size:24px;"></i>
                  <span style="font-size:20px;">&nbsp;TV</span><br><br>
                  <i class="fa fa-bath pr-2" style="font-size:24px;"></i>
                  <span style="font-size:20px;">&nbsp;욕조</span><br><br>
                  <i class="fa fa-cutlery pr-2" style="font-size:24px;"></i>
                  <span id="bbq" class="" v-if="this.intinfo.barbecue==0" style="font-size:20px;"><del style="diplay:inline;">바베큐</del></span>
                  <span id="bbq" class="" v-if="this.intinfo.barbecue!=0" style="font-size:20px;">바베큐</span><br><br>
                </div>
                <div class="box3">
                  <i class="fa fa-wifi pr-2" style="font-size:24px;"></i>
                  <span style="font-size:20px;"> 무선인터넷</span><br><br>
                  <i class="fa fa-snowflake-o pr-2 pl-1" style="font-size:24px;"></i>
                  <span style="font-size:20px;"> 냉장고</span><br><br>
                  <i class="fa fa-desktop" style="font-size:24px;"></i>
                  <span class="pl-3" style="font-size:20px;"><del style="display:inline;">컴퓨터</del></span><br><br>
                  <i class="fa fa-taxi pr-3" style="font-size:24px;"></i>
                  <span v-if="(!intinfo) || (intinfo && !intinfo.pickup) || (intinfo && intinfo.pickup.includes('불'))" style="font-size:20px;"><del style="diplay:inline;">픽업</del><br><br></span>
                  <span v-if="intinfo && intinfo.pickup && !intinfo.pickup.includes('불')" style="font-size:20px;"> 픽업<br><br></span>
                </div>
              </div>
            </div>
          </div>
          </div>
          <div class="box2">
            <div class="rFormm">
              <!-- <div style="float:left;"><span>{{roominfo[0].roomoffseasonminfee1}}</span></div> -->
              <div class="rForm sm-6">
              <div>체크인</div>
              <input type="date" v-model="cin">
              </div>
              <div class="rForm sm-6">
              <div>체크아웃</div>
              <input type="date" v-model="cout">
              </div>
            <div class="">
              <div style="font-size:14px; float:left;">인원</div>
              <div style="clear:both; float:left">
                <span class="pr-5">게스트 {{count}}명 (최대 4명) &nbsp;</span>
                <b-button @click='cMinus'> - </b-button><b-button @click='cFlus'> + </b-button>
              </div>
            </div>
              <div><b-button variant="info" class="text2">예약하기</b-button></div>
              <div class="" v-if="cin!='' && cout!=''">
                <div class="" style="overflow:hidden;">
                  <div class="pl-2 box_left" v-if="roominfo">
                    {{roominfo[0].roomoffseasonminfee1}}(원) X {{day}}박 
                  </div>
                  <div class="pl-2 box_left" v-if="!roominfo">
                    62000(원) X {{day}}박 
                  </div>
                  <div class="pr-2 box_right">
                    {{roomPrice}}(원)
                  </div>
                </div>
                <div class="pt-2 box_line">
                  <div class="pl-2 box_left">
                    {{countP}}(원)  X {{ckh}}명<span style="font-size:10px">(추가인원)</span>
                  </div>
                  <div class="pr-2 pb-2 box_right">
                    {{countPrice}}(원)
                  </div>
                </div>
                <div class="pt-2">
                  <div class="pl-2 box_left">
                    합계
                  </div>
                  <div class="pr-2 pb-2 box_right">
                    {{totalPrice}}(원)
                  </div>
                </div>
              </div>  
            </div>
          </div>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      count:1,
      cin:'',
      cout:'',
      day:'',
      roomPrice:'',
      countPrice:0,
      countP:0,
      ckh:0,
    }
  },
  props:[
    'cominfo','intinfo','roominfo',
  ],
  methods: {

    cFlus(){
      if(this.count==4){
        this.count=4;
      }else{
        this.count++;
      }
      if(this.count>2){
        this.countPrice = (this.count-2) *10000;
        this.countP=10000;
        this.ckh = this.count-2;
      }
    },
    cMinus(){
      if(this.count==1){
        this.count=1;
      }else{
        this.count--;
      }
      if(this.count<=2){
        this.countPrice = 0;
        this.countP=0;
        this.cjh=0;
      }
    }
  },
  watch:{
    cin:function(){
      if(this.cout!=''){
        var sdt = new Date(this.cout)
        var edt = new Date(this.cin);
        var diff = Math.ceil((sdt.getTime()-edt.getTime())/(1000*3600*24));
        this.day = diff;
        if(typeof(this.roominfo) != "undefined"){
            this.roomPrice = this.roominfo[0].roomoffseasonminfee1 * this.day; 
        }
        else{
          this.roomPrice = 62000 * this.day; 
        }
      }
    },
    cout:function(){
      if(this.cin!=''){
        var sdt = new Date(this.cout)
        var edt = new Date(this.cin);
        var diff = Math.ceil((sdt.getTime()-edt.getTime())/(1000*3600*24));
        this.day = diff;

        if(typeof(this.roominfo) != "undefined"){
            this.roomPrice = this.roominfo[0].roomoffseasonminfee1 * this.day; 
        }
        else{
          this.roomPrice = 62000 * this.day; 
        }
      }
    },
    roomPrice:function(){
      this.totalPrice = this.roomPrice + this.countPrice;
    },
    countPrice:function(){
      this.totalPrice = this.roomPrice + this.countPrice;
    }
  },
  created(){
   
  }
}
</script>
<style scoped>
.detail{
  display: inline;
}
.detailform{
  float:left;
}
.stay_menu{
  padding: 5px;
}
.first{
  width:800px;
  height:300px;
}
.other{
  height:150px;
  width:400px;
  padding-right: 5px;
  padding-left:  5px;
  padding-bottom: 5px;
}
.other2{
  height:150px;
  width:400px;
  padding-right: 5px;
  padding-left:  5px;
}
.box1{
  border-bottom: 1px solid lightgray;
  float: left;
  text-align: left;
  width:60%;
  overflow: hidden;
}
.box2{
  float: right;
  width:40%;
}
.box3{
  width:50%;
  overflow: hidden;
  float:left;
}
.box{
  float:left;
  overflow: hidden;
}
.box_main{
  clear: both;

}
.intro{
  font-size: 20px;;
}
.nob{
  text-decoration: line-through;
}
.box_line{
  border-bottom: 1px solid lightgray;
  width:100%;
  overflow:hidden;
}
#wrapper{
  display:flex;
}
.box_left{
  display: inline;
  float:left;
  font-size: 18px; 
}
.box_right{
  display: inline;
  float: right;
  font-size: 18px; 
}
span{
  font-family: 'Do Hyeon', sans-serif;
}
</style>