import store from '@/store/index';

export function setInterceptors(instance) {
  instance.interceptors.request.use(
    function (config) {
      console.log('header add auth!:'+store.state.memberLoginInfo.token)
      config.headers.Authorization = store.state.memberLoginInfo.token;
      return config;
    },
    function(error) {
      return Promise.reject(error);
    },
  );

  // Add a response interceptor
  instance.interceptors.response.use(
    function(response) {
      return response;
    },
    function(error) {
      return Promise.reject(error);
    },
  );

  return instance;
}

