<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">공지사항</h2>
      </div>
    </div>
    <div class="section">
      <div class="container">
        <h3 class="title">{{notice.ntitle}}</h3>
        <p v-html="notice.ncontent"></p>
        <button class="btn btn-neutral"  style="color:blue" v-if="memberLoginInfo.info.role == 'admin'" @click="moveDelete(notice.nno)">삭제</button>
        <button class="btn btn-neutral"  style="color:blue" v-if="memberLoginInfo.info.role == 'admin'" @click="moveUpdate">수정</button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  computed:{
    ...mapState(["notice", 'memberLoginInfo'])
  },
  methods: {
    moveUpdate(){
      this.$router.push("/notice/update")
    },
    moveDelete(nno){
      console.log(nno);
      this.$store.dispatch("deleteNotice", nno).then(() => {
        this.$store.dispatch("getNoticeList");
        this.$router.push("/notice");
      })
    }
  },
  created() {
    // this.$store.dispatch("getNotice", nno);
  },
}
</script>
