<template lang="">
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">숙박 정보</h2>
      </div>
    </div>
    <div class="section section-about-us">
      <div class="container">
        <div class="row">
          <div class="ml-auto mr-auto text-center detailform">
          <!-- <ul class="">
            <li class="stay_menu on" id='com1'><a href="" @click.prevent="comInfo"><span>공통정보</span></a></li>
            <li class="stay_menu" id='com2'><a href="" @click.prevent="intInfo"><span>소개정보</span></a></li>
            <li v-if="roomInfo!=''" class="stay_menu" id='com3'><a href="" @click.prevent="roomInfo"><span>방 정보</span></a></li>
          </ul> -->
                    
            <com-info v-if="comC" :cominfo="stayDetail" :intinfo="stayInt" :roominfo="stayRoom"></com-info>
            <!-- <int-info v-else-if="intC" :intInfo="stayInt"></int-info>
            <room-info v-else :romInfo="stayRoom"></room-info> -->
          </div>
          <hotel-map :lat="stayDetail.mapy" :lng="stayDetail.mapx"></hotel-map> 
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ComInfo from '@/components/ComInfo.vue';
import HotelMap from '@/components/HotelMap.vue';

import {mapActions} from 'vuex';
import {mapGetters} from 'vuex';
export default {
  data(){
    return{
      comC : true,
      intC : false,
      romC : false,
    }
  },
  components:{
    ComInfo,HotelMap,
  },
  computed:{
    ...mapGetters(["stayDetail"]),
    ...mapGetters(["stayInt"]),
    ...mapGetters(["stayRoom"]),
  },
  methods: {
    ...mapActions(["getStayDetail"]),
    ...mapActions(["getStayInt"]),
    ...mapActions(["getStayRoom"]),
    SetDetail(content){
      this.getStayDetail(content);
    },
    comInfo(){
      this.comC=true;
      this.intC=false;
      this.romC=false;
      document.getElementById('com1').className +=' on';
      document.getElementById('com2').classList.remove('on');
      document.getElementById('com3').classList.remove('on');
    },
    intInfo(){
      this.comC=false;
      this.intC=true;
      this.romC=false;
      document.getElementById('com2').className +=' on';
      document.getElementById('com1').classList.remove('on');
      document.getElementById('com3').classList.remove('on');
    },
    roomInfo(){
      this.comC=false;
      this.intC=false;
      this.romC=true;
      document.getElementById('com3').className +=' on';
      document.getElementById('com1').classList.remove('on');
      document.getElementById('com2').classList.remove('on');
    },
    SetInt(content){
      this.getStayInt(content);
    },
    SetRoom(content){
      this.getStayRoom(content);
    },

  },
  created() {
    this.SetDetail(this.$route.query.contentid);
    let a = this.$route.query.contentid;
    let b = this.$route.query.contentTypeid;
    let content={
      contentid: a,
      contentTypeid: b,
    }
    this.SetInt(content);
    this.SetRoom(content);
  },
}
</script>
<style>
.detail{
  display: inline;
}
.detailform{
  float:left;
}
.stay_menu{
  float: left;
  padding: 5px;
  width: 30%;
  border: 1px solid lightgray;
}
.on{
  background-color: lightslategrey;
  color:white;
}
a.hover{
  color:white;
}
.rFormm{
  overflow: hidden;
  float:right;
  border: 1px solid lightgrey;
  border-radius: 5px;
  position: sticky;
  margin-top:30px;
  top: 100px;
}
.rForm{
  padding: 5px;
  display: inline-block;  
  overflow: hidden;
  border: 1px solid lightgray;
}
.test{
  border: 1px solid lightgray;
}
.text2{
  width: 100%;
}
</style>