<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">프로필</h2>
      </div>
    </div>
    <div class="section">
      
      <div class="container">
        <h3 class="title">사용자 정보 수정</h3>
        <form>
          <div class="form-group d-flex flex-column">
            <label for="muserid">아이디:</label>
            <input type="muserid" class="form-control" placeholder="아이디를 입력하세요" id="muserid" v-model="myModifyInfo.info.userid" disabled>
          </div>
          <div class="form-group d-flex flex-column">
            <label for="userpwd">비밀번호:</label>
            <input type="text" class="form-control" placeholder="비밀번호를 입력하세요" id="userpwd" v-model="myModifyInfo.info.userpwd">
          </div>
          <div class="form-group  d-flex flex-column">
            <label for="username">닉네임:</label>
            <input type="text" class="form-control" placeholder="닉네임을 입력하세요" id="username" v-model="myModifyInfo.info.username">
          </div>
          <div class="form-group  d-flex flex-column">
            <label for="email">이메일:</label>
            <input type="text" class="form-control" placeholder="이메일을 입력하세요" id="email" v-model="myModifyInfo.info.email">
          </div>
          <div class="form-group  d-flex flex-column">
            <label for="address">주소:</label>
            <input type="text" class="form-control" placeholder="주소를 입력하세요" id="address" v-model="myModifyInfo.info.address">
          </div>
          <div class="form-group d-flex flex-column">
            <label for="age">나이:</label>
            <select name="age" id="age" v-model="myModifyInfo.info.age">
              <option value=10>10대</option>
              <option value=20 selected>20대</option>
              <option value=30>30대</option>
              <option value=40>40대</option>
              <option value=50>50대</option>
              <option value=60>60대</option>
            </select>
          </div>  
          <button type="submit" class="btn btn-neutral" style="color:blue" @click.prevent="moveSubmit">수정하기</button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';

export default {
  name: 'profile',
  bodyClass: 'profile-page',
  components: {
  },
  created() {
    this.myModifyInfo.status = {...this.memberLoginInfo.status};
    this.myModifyInfo.info = {...this.memberLoginInfo.info};
    this.myModifyInfo.token = {...this.memberLoginInfo.token};
    console.log(this.myModifyInfo)
  },
  computed:{
    ...mapGetters(['memberLoginInfo'])
  },
  data() {
    return {
      myModifyInfo: {
        status: false,
        info: {},
        token: '',
      },
    }
  },
  methods: {
    moveSubmit(){
      if(this.registerInfo.userid.trim() == '' || this.registerInfo.userpwd.trim() == '' ||
        this.registerInfo.username.trim() == '' || this.registerInfo.email.trim() == '' ||
        this.registerInfo.address.trim() == ''){
          alert('모든 항목을 다 입력해야합니다.')
      }else{
        this.$store.dispatch("putModify", this.myModifyInfo).then((res) => {
          console.log('수정됨');
          console.log(res);
          if(res.data.status){
            this.$store.dispatch("setmemberLoginInfo", {
              status: res.data.status,
              info: res.data.data,
              token: res.data.jwt_auto_token
            })
            this.$router.push("/profile");
          }
        })
      }
    }
  },
};
</script>
<style></style>
