<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">숙박 정보</h2>
      </div>
    </div>
    <div class="section section-about-us">
      <div class="container">
        <div class="">
          <div class="col-md-8 ml-auto mr-auto text-center">
            <h2 class="title" style="font-family: 'Do Hyeon', sans-serif;">조건에 맞는 호텔을 검색해보세요</h2>
            <div class="container align-center">
              <div class="" style="text-align:center">
                <!-- <fg-input class="col-9 mt-3" placeholder="Simple input with v-model binding" v-model="inputVal"></fg-input> -->
                <!-- <div class="col-1"></div> -->
                <select v-model="opt" style="height:38px;">
                  <option value="1">서울</option>
                  <option value="2">인천</option>
                  <option value="3">대전</option>
                  <option value="4">대구</option>
                  <option value="5">광주</option>
                  <option value="6">부산</option>
                  <option value="7">세종특별자치시</option>
                  <option value="8">경기도</option>
                  <option value="9">강원도</option>
                  <option value="10">충청북도</option>
                  <option value="11">충청남도</option>
                  <option value="12">경상북도</option>
                  <option value="13">전라북도</option>
                  <option value="14">전라남도</option>
                  <option value="15">제주도</option>
                </select>
                <n-button class="col-2" @click.prevent="searchList">Search</n-button>
              </div>
              <p></p>
            </div>
          </div>
          <div>
              <ul>
                  <li class="sItem" v-for="(item,index) in stay.items.item" :key="index">
                    <a class="itm" href="" @click="detailInfo(item.contentid,item.contenttypeid)">
                    <img v-if="item.firstimage" :src="item.firstimage"/>
                    <img v-else src="img/noimage.png"/>
                    <p>{{item.title}}</p>
                    </a>
                </li>
              </ul>
          </div>
              <div class="container col-sm-10 pt-2" style="text-align:center; clear:both;">
                <ul class="d-inline-flex">
                  <li class="page-item"><a class="page-link" href="#" @click="sendPage(page-1)">Previous</a></li>
                  <li class="page-item" v-for="(p, index) in plist()" :key="`${index}_plist`">
                    <a class="page-link" href="#" @click="sendPage(p)">{{p}}</a>
                  </li>
                  <li class="page-item"><a class="page-link" href="#" @click="sendPage(page+1)">Next</a></li>
                </ul>
              </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, FormGroupInput } from '@/components';
import {mapGetters} from 'vuex';
import {mapActions} from 'vuex';
export default {
  name: 'landing',
  bodyClass: 'landing-page',
  components: {
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      form: {
        firstName: '',
        email: '',
        message: ''
      },
      page:'1',
      opt:'1',
      plist: function () {
          var list = [];
          var start;
          if((this.page-1)/10 > 0){
            //console.log(this.page);
            start = parseInt((this.page-1)/10) * 10 +1;
            //console.log(start);
          }
          else{
            start = 1;
          }
          var end = start+9;  
          var a = this.stay.totalCount/12;
          if(this.stay.totalCount%12>0){
            a++;
          }
          if(end>a){
            end=a;
          }
          
          
          for (var i = start; i <= end; i++)
            list.push(i);
          return list;
      }
    };
  },
  computed: {
    ...mapGetters(["stay"]),
  },
  methods:{
    ...mapActions(["getStay"]),
    selectList(page){
      this.getStay(page);
    },
    sendPage(page){
      if(page<1){
        page=1;
      }
      else if(this.totalCount%12>0 && ((this.totalCount/12) + 1) < page){
        page = (this.totalCount/12)+1;
      }
      else if(this.totalCount/12 < page){
        page = this.totalCount/12;
      }
      let pageInfo={
        opt:this.opt,
        page:page,
      }
      this.selectList(pageInfo);
      this.page=page;
    },
    detailInfo(info,type){
      this.$router.push("/searchHotel/detail?contentid="+info+"&contentTypeid="+type);
    },
    searchList(){
      let pageInfo={
        opt:this.opt,
        page:1,
      }
      this.selectList(pageInfo);
    }
  },
  created() {
    let pageInfo={
      opt:1,
      page:1,
    }
    this.selectList(pageInfo);
  },
};
</script>
<style scoped>
ul{
  list-style-type: none;
  text-align: center;
}
.sItem{
  overflow: hidden;
  float:left;
  width:25%;
  height:200px;
}
img{
  height: 125px;
  width: 210px;
}
.row{
  margin: 40px 0 0 -39px;
}
p{
  display: block;
  margin-top: 1rem;
  margin-bottom: 1rem;
  
}
.itm{
  color:black;
}
.itm:hover{
  color: black;
}
</style>