<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">여행 코스 정보</h2>
      </div>
    </div>
    <div class="section">
      <div class="container" style="width: 1000px;">
        <div>
          <h2 style="font-family: 'Do Hyeon', sans-serif;">{{tripDetailInfoCommon.title}}</h2>
          <p style="font-family: 'Do Hyeon', sans-serif;">코스 설명</p>
          <p>{{tripDetailInfoCommon.overview}}</p>
          <p><strong style="font-family: 'Do Hyeon', sans-serif;">거리: </strong> {{tripDetailInfoIntro.distance}}</p>
          <p><strong style="font-family: 'Do Hyeon', sans-serif;">소요시간: </strong>{{tripDetailInfoIntro.taketime}}</p>
        </div>
        <hr>
        <div class="section-tabs bg-white">
          <div class="container">
            <h3 class="title" style="font-family: 'Do Hyeon', sans-serif;">코스 정보
            </h3>
            <div class="row">
              <div class="">
                <!-- Nav tabs col-md-10 ml-auto col-xl-6 mr-auto-->
                <card>
                  <tabs
                    slot="raw-content"
                    tab-content-classes="tab-content-padding text-center"
                  >
                    <tab-pane v-for="(item, index) in tripDetailInfoCourse" :key="index">
                      <template slot="label">
                        &#127880; {{index + 1}} 코스
                      </template>
                      <h2 style="font-family: 'Do Hyeon', sans-serif;">{{item.subname}}</h2>
                      <img class="img-detail" v-if="item.subdetailimg" :src="item.subdetailimg"/>
                      <img class="img-detail" v-else src="/img/my/cat_no_image.jpg"/>
                      <p class="mt-3" style="font-size: 20px;">{{item.subdetailoverview}}</p>
                    </tab-pane>
                  </tabs>
                </card>
              </div>
            </div>
          </div>
        </div>
        <hr>
        <h3 class="title" style="font-family: 'Do Hyeon', sans-serif;">지도 정보</h3>
        <div id="map" style="width:970px;height:600px"></div>
      </div>
    </div>
  </div>
</template>
<script>
import { Card, Tabs, TabPane } from '@/components';
import {mapGetters} from 'vuex';

export default {
  components: {
    Card,
    Tabs,
    TabPane
  },
  computed: {
    ...mapGetters(['tripDetailInfoCommon', 'tripDetailInfoIntro', 'tripDetailInfoCourse', 'tripMapInfo']),
  },
  created() {
    
  },
  mounted() {
    if(window.kakao && window.kakao.maps){
      this.initMap()
    }else{ 
      this.addScript();
    }
  },
  methods: {
    initMap() {
        console.log("x : " + this.tripMapInfo.mapx + " y: " + this.tripMapInfo.mapy)
        var mapContainer = document.getElementById('map'); // 지도를 표시할 div
        var mapOption = {
            center: new kakao.maps.LatLng(parseFloat(this.tripMapInfo.mapy),parseFloat(this.tripMapInfo.mapx)), // 지도의 중심좌표
            level: 3, // 지도의 확대 레벨
        };

        var map = new kakao.maps.Map(mapContainer, mapOption);

        var marker = new kakao.maps.Marker({
            map: map, // 마커를 표시할 지도
            position: new kakao.maps.LatLng(parseFloat(this.tripMapInfo.mapy),parseFloat(this.tripMapInfo.mapx)), // 마커를 표시할 위치
        });
        marker.setMap(map);
      },
      addScript() {
        const script = document.createElement('script');
        script.onload = () => kakao.maps.load(this.initMap); 
        script.src = 'http://dapi.kakao.com/v2/maps/sdk.js?appkey=e9d25545815b9fa054fba77bf7aa2e29'; 
        document.head.appendChild(script); 
    },
  },
  watch:{
    tripMapInfo(){
      if(window.kakao && window.kakao.maps){
        this.initMap()
      }else{ 
        this.addScript();
      }

      this.initMap();
    }
  }
};
</script>
<style>
.tab-content.tab-content-padding {
  padding: 20px;
}

.img-detail {
  width: 900px;
  height: 600px;
}
</style>
