<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white">공지사항 등록</h2>
      </div>
    </div>
    <div class="section">
      <div class="container">
        번호: <input type="text" v-model="notice.nno"><br>
        제목: <input type="text" v-model="notice.ntitle"><br>
        내용: <ckeditor v-model="notice.ncontent" :config="editorConfig"></ckeditor>
        <button class="btn btn-neutral"  style="color:blue" @click="add">작성완료</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      notice:{
        nno: 0,
        ntitle: '',
        ncontent: '',
      },
      editorConfig:{
        language: 'ko'
      }
    }
  },
  methods: {
    add(){
      if(this.notice.ntitle.trim() == '' || this.notice.ncontent.trim() == ''){
        alert('모든 항목을 다 입력해야됩니다.')
      }else{
        this.$store.dispatch("postNotice", this.notice).then(() => {
          this.$router.push("/notice");
        }).catch(() => {
          alert('해당 게시글 번호를 사용하지못합니다. 다른 번호를 입력해주세요');
        })
      }
    }
  },
}
</script>
